import "./Closet.css";
import { logoutUser } from "../../api/user";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import Header from "../../components/Header/Header";
import ClosetItem from "../../components/ClosetItem/ClosetItem";
import {
	selectUserCloth,
	fetchUserClothes,
	UserClothType,
} from "../../store/slices/userCloth";
import { AppDispatch } from "../../store";
import Modal from "react-modal";

import AddClothModal from "../../components/AddClothModal/AddClothModal";
// import ClothDetailModal from '../../components/ClothDetailModal/ClothDetailModal'

type ClosetItem = {
	id: number;
	source_url: string;
	type: string;
	color: string;
	pattern: string;
};

export default function Closet() {
	const type_tree =  [
		[[''], [' ', '/ ', ' ', '/', ' ', ' ', '/', '/', ' ']], 
		[[''], [' ', ' ', ' ', '', ' /', ' /', '/ ', ' ']], 
		[[''], [' ', ' ', '/MA-1', '  ', '/ ', '  ',
				'/', '  ', '/ ', ' ', '/ ', '/ ', '', ' ', ' ',
				'', '/', '/ ', ' ', '/ ', ' ', ' ']]]
	
	const TYPEOPTIONS = [
		{ value: "Type" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
	];
	
	const navigate = useNavigate();
	const userClothState = useSelector(selectUserCloth);
	const dispatch = useDispatch<AppDispatch>();

	const [filterOption, setFilterOption] = useState("Type");
	const [addClothModalOpen, setAddClothModalOpen] = useState(false);
	const [submitted, setSubmitted] = useState<boolean>();
	const [filteredList, setFilteredList] = useState<UserClothType[]>([]);

	const clickAddClothPopupHandler = () => {
		setAddClothModalOpen(true);
	};

	const clickAddClothPopupCloseHandler = (metaType: string) => {
		setAddClothModalOpen(!addClothModalOpen);
		setFilterOption(metaType); //  metatype return setfilteroption
		setSubmitted(true);
	};

	const clickOnDeleteHandler = (metaType: string) => {
		setFilterOption(metaType); //  metatype return setfilteroption
		setSubmitted(true);
	};

	useEffect(() => {
		dispatch(fetchUserClothes());
		filter_list('Type');
	}, []);

	useEffect(() => {
		// alert('')
		dispatch(fetchUserClothes());
		filter_list(filterOption); // setFilterOption  set filteroption 
		setSubmitted(false);
	}, [submitted]);

	//for logout
	const [isSending, setIsSending] = useState(false)
	const checkLoginned = () => {
		if(localStorage.getItem("username") !== null){
			return true
		}
		else return false
	};

	useEffect(() => {
		const redirect = () => {
			if (!checkLoginned()) {
				navigate("/");
			}
		};
		redirect();
	}, [isSending]);

	const getType = (t:string) => {
		for(let i =0; i< type_tree.length; i++){
			if(type_tree[i][1].includes(t)){
				return type_tree[i][0]
			}
		}
		return ""
	}

	const filter_list = (t:string) => {
		if(t === 'Type'){
			setFilteredList(userClothState.userClothes)
		}
		else{
			const tmpUserCloth = userClothState.userClothes.filter((cloth) => { return t == getType(cloth.type); })
			// alert(JSON.stringify(tmpUserCloth))
			setFilteredList(tmpUserCloth)
		}
	}

	return (
		<div className="Closet">
			<div className="Closet-header">
				<Header
					clickInfoHandler={() => {
						navigate("/setting");
					}}
					clickLogoutHandler={async() => {
						await logoutUser().catch((error) => console.log(error))
						setIsSending(!isSending)
					}}
					clickHeaderHandler={() => {
						navigate("/home");
					}}
				></Header>
			</div>

			<div className="ClosetTop">
				<div className="ClosetDiv">
					<div className="ClosetHead">
						<text id="Closet-text-main">Closet</text>
						<div id='Closet-select-div'>
							<select
								id="type-select"
								data-testid="select-component"
								// defaultValue="Type"
								onChange={(e) => {filter_list(e.target.value)}}
							>
								{TYPEOPTIONS.map((option, index) => (
									<option key={index} value={option.value} >
										{option.value}
									</option>
								))}
							</select>
						</div>
						<button id="add-cloth-button" data-testid="add-cloth-button" onClick={clickAddClothPopupHandler}>
							Add
						</button>
						<Modal
							id="add-cloth-modal"
							data-testid="add-cloth-modal"
							isOpen={addClothModalOpen}
							// onRequestClose={() => setAddClothModalOpen(false)}
						>
							<AddClothModal
								modal_close={clickAddClothPopupCloseHandler}
							></AddClothModal>
							<div id="close-buton-div">
								<button
									id="modal-close-button"
									data-testid="modal-close-button"
									onClick={() => {
										setAddClothModalOpen(false);
									}}
								>
									
								</button>
							</div>
						</Modal>
					</div>
					<div className="ClosetItems">
					{
						filteredList.length !== 0 ? filteredList.map((cloth, index) => {
							return(
								<ClosetItem
									key={index}
									user_cloth_id={String(cloth.id)}
									source_url={cloth.image_link}
									weardate={cloth.dates}
									metatype={cloth.name}
									type={cloth.type}
									color={cloth.color}
									pattern={cloth.pattern}
									tmp={clickOnDeleteHandler}
								/>
							)
						}) :
						<div>
							<text id='add-cloth-text'> !</text>
						</div>
					}
					</div>
				</div>
			</div>
		</div>
	);
}
