import React, { useState, useEffect } from "react";

export interface IProps {
	metaType: string | null;
	selectHandler: (value: string) => void;
}

const TypeFilter = (props: IProps) => {
	const [metaType, setMetaType] = useState<string | null>(props.metaType);

	const TopTypeOptions = [
		{ value: " " },
		{ value: " " },
		{ value: "/ " },
		{ value: " " },
		{ value: "/" },
		{ value: " " },
		{ value: " " },
		{ value: "/" },
		{ value: "/" },
		{ value: " " },
	];

	const BottomTypeOptions = [
		{ value: " " },
		{ value: " " },
		{ value: " " },
		{ value: " " },
		{ value: "" },
		{ value: " /" },
		{ value: " /" },
		{ value: "/ " },
		{ value: " " },
	];

	const OuterTypeOptions = [
		{ value: " " },
		{ value: " " },
		{ value: " " },
		{ value: "/MA-1" },
		{ value: "  " },
		{ value: "/ " },
		{ value: "  " },
		{ value: "/" },
		{ value: "  " },
		{ value: "/ " },
		{ value: " " },
		{ value: "/ " },
		{ value: "/ " },
		{ value: "" },
		{ value: " " },
		{ value: " " },
		{ value: "" },
		{ value: "/" },
		{ value: "/ " },
		{ value: " " },
		{ value: "/ " },
		{ value: " " },
		{ value: " " },
	];

	const NullTypeOptions = [{ value: " " }];

	useEffect(() => {
		setMetaType(props.metaType);
	}, [props]);

	if (metaType == null) {
		return (
			<>
				<select id="type-select" disabled={true}>
					{NullTypeOptions.map((option, index) => (
						<option key={index} value={option.value}>
							{option.value}
						</option>
					))}
				</select>
			</>
		);
	} else if (metaType == "") {
		return (
			<>
				<select id="type-select" onChange={(e) => props.selectHandler(e.target.value)}>
					{TopTypeOptions.map((option, index) => (
						<option
							key={index}
							value={option.value}
							// onClick={() => props.selectHandler(option.value)}
						>
							{option.value}
						</option>
					))}
				</select>
			</>
		);
	} else if (metaType == "") {
		return (
			<>
				<select id="type-select" onChange={(e) => props.selectHandler(e.target.value)}>
					{BottomTypeOptions.map((option, index) => (
						<option
							key={index}
							value={option.value}
							// onClick={() => props.selectHandler(option.value)}
						>
							{option.value}
						</option>
					))}
				</select>
			</>
		);
	} else {
		return (
			<>
				<select id="type-select" onChange={(e) => props.selectHandler(e.target.value)}>
					{OuterTypeOptions.map((option, index) => (
						<option
							key={index}
							value={option.value}
							// onClick={() => props.selectHandler(option.value)}
						>
							{option.value}
						</option>
					))}
				</select>
			</>
		);
	}
};

export default TypeFilter;
