import React from "react";
import "./FilterModal.css";
import { useState } from "react";
import TypeFilter from "../TypeFilter/TypeFilter";

export interface IProps {
	clickDoneHandler: (
		type: string | null,
		color: string | null,
		pattern: string | null
	) => void;
}

const FilterModal = (props: IProps) => {
	const [type, setType] = useState<string | null>(null);
	const [color, setColor] = useState<string | null>(null);
	const [pattern, setPattern] = useState<string | null>(null);
	const [metaType, setMetaType] = useState<string | null>(null);

	const ColorOptions = [
		{ value: "Color"},
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
	];

	const PatternOptions = [
		{ value: "Pattern"},
		{ value: "None" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
		{ value: "" },
	];

	const MetaTypeOptions = [
		{ value: " " },
		{ value: "" },
		{ value: "" },
		{ value: "" },
	];

	const clickMetaTypeOptionHandler = (value: string) => {
		if (value == " ") {
			setMetaType(null);
		} else {
			setMetaType(value);
		}
	};

	const clickTypeOptionHandler = (value: string) => {
		if (
			value == " " ||
			value == " " ||
			value == " "
		) {
			setType(null);
		} else setType(value);
	};

	const clickColorOptionHandler = (value: string) => {
		if (value == "Color") {
			setColor(null);
		} else setColor(value);
	};

	const clickPatternOptionHandler = (value: string) => {
		if (value == "Pattern") {
			setPattern(null);
		} else setPattern(value);
	};

	return (
		<div className="FilterModal">
			<div>
				<select id="type-select"
				onChange={(e) => clickMetaTypeOptionHandler(e.target.value)}>
					{MetaTypeOptions.map((option, index) => (
						<option
							key={index}
							value={option.value}
						>
							{option.value}
						</option>
					))}
				</select>
				<TypeFilter
					data-testid="typefilter"
					metaType={metaType}
					selectHandler={clickTypeOptionHandler}
				></TypeFilter>
			</div>
			<select id="color-select"
			onChange={(e) => clickColorOptionHandler(e.target.value)}>
				{ColorOptions.map((option, index) => (
					<option
						key={index}
						value={option.value}
						onClick={() => {clickColorOptionHandler(option.value)}}
					>
						{option.value}
					</option>
				))}
			</select>

			<select id="pattern-select"
			onChange={(e) => clickPatternOptionHandler(e.target.value)}>
				{PatternOptions.map((option, index) => (
					<option
						key={index}
						value={option.value}
						onClick={() => {clickPatternOptionHandler(option.value)}}
					>
						{option.value}
					</option>
				))}
			</select>

			<button
				id="done-button"
				data-testid="done-button"
				onClick={() => props.clickDoneHandler(type, color, pattern)}
			>
				Done
			</button>
		</div>
	);
};

export default FilterModal;
