import "./SampleClothModal.css";
import React from "react";
import { useNavigate } from "react-router-dom";

export interface Iprops {
	userHave: boolean;
	userCloth_url: string | undefined;
	sampleCloth_url: string | undefined;
	type: string | undefined;
	color: string | undefined;
	pattern: string | undefined;
	sampleCloth_name: string | undefined;
	sampleCloth_link: string | undefined;
}

const SampleClothModal = (props: Iprops) => {
	const navigate = useNavigate();

	const clickPurchaseButtonHander = () => {
		if (props.sampleCloth_link as string){
			navigate("/Redirect", { state: { url: props.sampleCloth_link } });
		}
		else{
			navigate('*');
		}
	};

	if (props.userHave) {
		return (
			<div className="SampleClothModal">
				<text id="samplecloth-title">Clothes Data</text>
				<div className="Body">
					<div className="sampleDiv">
						<img className="sample-image" data-testid = "no-sample-image-text" src={props.sampleCloth_url} alt='  .'></img>
						<text id="sample-image-title"> </text>
					</div>
					<div className="userDiv">
						<img className="user-image" src={props.userCloth_url} alt = '   .'></img>
						<text id="user-image-title"> </text>
					</div>
					<div className="DataDiv">
						<button
							id="purchase-button"
							onClick={() => clickPurchaseButtonHander()}
						>
							Purchase Button
						</button>
						{
							props.sampleCloth_name?
							<text id="cloth-name">{props.sampleCloth_name}</text>
							:
							<text id = 'cloth-name'>  .</text>
						}
						<div className="ClothTags">
						{
							props.type?
							<text id="cloth-type">Type : {props.type}</text>
							:
							<text id="cloth-type">Type :     .</text>
						}
						{
							props.color?
							<text id="cloth-color">Color : {props.color}</text>
							:
							<text id="cloth-color">Color :     .</text>
						}
						{
							props.pattern?
							<text id="cloth-pattern">Pattern : {props.pattern}</text>
							:
							<text id="cloth-pattern">Pattern :     .</text>
						}
						</div>
					</div>
				</div>
			</div>
		);
	} else {
		return (
			<div className="SampleClothModal">
				<text id="samplecloth-title">Clothes Data</text>
				<div className="Body">
					<div className="sampleDiv">
						<img className="sample-image" src={props.sampleCloth_url} alt='  .'></img>
						<text id="sample-image-title"> </text>
					</div>
					<div className="DataDiv">
						<button
							id="purchase-button"
							onClick={() => clickPurchaseButtonHander()}
						>
							Purchase Button
						</button>
						{
							props.sampleCloth_name?
							<text id="cloth-name">{props.sampleCloth_name}</text>
							:
							<text id = 'cloth-name'>  .</text>
						}
						<div className="ClothTags">
						{
							props.type?
							<text id="cloth-type">Type : {props.type}</text>
							:
							<text id="cloth-type">Type :     .</text>
						}
						{
							props.color?
							<text id="cloth-color">Color : {props.color}</text>
							:
							<text id="cloth-color">Color :     .</text>
						}
						{
							props.pattern?
							<text id="cloth-pattern">Pattern : {props.pattern}</text>
							:
							<text id="cloth-pattern">Pattern :     .</text>
						}
						</div>
					</div>
				</div>
			</div>
		);
	}
};

export default SampleClothModal;